<template>
    <div class="chat" >
        <Konversation ref="conversation" :rtl="true" avatar="" timeout="500"/>
    </div>
</template>

<script>

import Konversation from 'konversation-vue'

export default {
    components: { Konversation },

    computed: {
        chat() {
            return this.$refs.conversation
        }
    },

    mounted() {

            this.chat
            .say('')
            .then(() =>   this.chat.say(' '))
            .then(() =>   this.chat.ask('  '))
            .then(name => this.chat.respond(`  ${name}`))

            .then(() =>   this.chat.say(''))
            .then(() =>   this.chat.say('  '))
            .then(() =>   this.chat.ask(' ', 'choice', { answers: [ { value: 0, label: '' }, { value: 1, label: '' } ]}))
            .then(gndr => this.chat.respond(` ${gndr.label}`))

            .then(() =>   this.chat.say('  '))
            .then(() =>   this.chat.ask('  ', 'file', { api: 'http://localhost:3000/profile' }))
            .then(file => this.chat.respond('  ', [{ label: '  ', href: file }]))

            .then(() =>   this.chat.say('     ', [{ label: '  ', href: 'https://www.bramley.com/BramleyTermsandConditionsofUse.pdf' }]))
    }
}
</script>

<style lang="css">
    body {
        background: #efefef;
    }

    .chat {
        max-width: 600px;
        margin: auto;
        background: white;
        padding: 25px;
        border-radius: 10px;
    }
</style>



